<template>
  <div class="index">
    <div class="search">
      <input type="text" placeholder="输入城市/行政区/酒店名..." />
      <span>搜索</span>
      <i></i>
    </div>
    <div id="container"></div>
    <div class="saomao">
      <div class="left">
        <img src="../assets/images/home_position@2x.png" />
      </div>
      <div class="center">
        <i></i>
        <span @click="saoma">扫码干衣</span>
      </div>
      <div class="right">
        <img src="../assets/images/home_order@2x.png" />
      </div>
    </div>
  </div>
</template>

<script src="http://res.wx.qq.com/open/js/jweixin-1.4.0.js" type="text/javascript">
</script>    
<script>
import axios from "axios";
import url from "@/axios/api.js";

export default {
  created() {
    //地图
    window.onLoad = function() {
      var map = new AMap.Map("container", {
        zoom: 20, //级别
        center: [114.043679, 22.640445]
      });
      var circle = new AMap.Circle({
        center: new AMap.LngLat(114.043679, 22.640445), // 圆心位置
        radius: 100, //半径
        borderWeight: 3,
        strokeColor: "#FF33FF",
        strokeOpacity: 1,
        strokeWeight: 6,
        strokeOpacity: 0.2,
        fillOpacity: 0.4,
        strokeStyle: "dashed",
        strokeDasharray: [10, 10],
        // 线样式还支持 'dashed'
        fillColor: "#1791fc",
        zIndex: 50
      });

      circle.setMap(map);
      // 缩放地图到合适的视野级别
      map.setFitView([circle]);

      var circleEditor = new AMap.CircleEditor(map, circle);

      circleEditor.on("move", function(event) {
        log.info("触发事件：move");
      });

      circleEditor.on("adjust", function(event) {
        log.info("触发事件：adjust");
      });

      circleEditor.on("end", function(event) {
        log.info("触发事件： end");
        // event.target 即为编辑后的圆形对象
      });
    };

    var url =
      "https://webapi.amap.com/maps?v=1.4.15&key=5d5ca79128cfcddc0941f88113a1db77&callback=onLoad";
    var jsapi = document.createElement("script");
    jsapi.charset = "utf-8";
    jsapi.src = url;
    document.head.appendChild(jsapi);
    //判断登录
    if (location.href.search("openid") != -1) {
      localStorage.setItem("openid", this.$route.query.openid);
    }
    if (
      localStorage.getItem("openid") == null ||
      localStorage.getItem("openid") == "null"
    ) {
      this.$router.push("/login");
    }
  },
  methods: {
    ajax() {
      let params = new URLSearchParams();
      params.append("openid", localStorage.getItem("openid"));
      params.append("goods_id", "242");
      params.append("goods_num", "1");
      params.append("action", "buy_now");
      params.append("address_id", "7");
      params.append("coupon_id", "");
      params.append("pay_points", "");
      params.append("user_money", "");
      params.append("user_note", "");
      /*    if (this.$route.query.action == "") {
        data.cart_ids = this.data.cart_ids.toString();
      } else {
        data.goods_id = this.$route.query.goods_id;
        data.goods_num = this.$route.query.goods_num;
        data.item_id = this.$route.query.item_id;
      } */


      //扫一扫 
      axios.post(url.addOrder, params).then(res => {
        wx.config({
          debug: true,
          appId: res.data.data.appId + "", //公众号的唯一标识
          timestamp: res.data.data.timeStamp + "", //生成签名的时间戳
          nonceStr: res.data.data.nonceStr + "", //生成签名的随机串
          signature: res.data.data.signType + "", //签名
          jsApiList: ["checkJsApi", "scanQRCode"] //需要使用的JS接口列表(我只需要调用扫一扫的接口，如有多个接口用逗号分隔)
        });
      });
    },
    saoma() {
      this.ajax();
      wx.scanQRCode({
        needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
        scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
        success: function(res) {}
      });
    }
  }
};
</script>



<style scoped lang="scss">
.index {
  padding: 10px;
  .search {
    display: flex;
    align-items: center;
    padding: 20px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 10px 0px rgba(241, 241, 241, 0.8);
    border-radius: 23px;
    position: absolute;
    top: 10px;
    z-index: 111;
    width: 94%;
    input {
      border: none;
      height: 30px;
      width: 72%;
      outline: none;
    }
    input:focus {
      border: none;
    }
    input::-webkit-input-placeholder {
      color: rgb(189, 189, 189);
    }
    input::-moz-input-placeholder {
      color: rgb(189, 189, 189);
    }
    input::-moz-input-placeholder {
      color: rgb(189, 189, 189);
    }
    input::-ms-input-placeholder {
      color: rgb(189, 189, 189);
    }
    span {
      margin-left: auto;
      margin-right: 20px;
      font-size: 15px;
      font-family: SourceHanSansCN-Regular;
      font-weight: 400;
      color: rgba(255, 76, 35, 1);
    }
    i {
      background: url(../assets/images/home_list@2x.png) no-repeat;
      width: 20px;
      height: 20px;
      background-size: 100% 100%;
      display: block;
    }
  }
  #container {
    width: 100%;
    height: 100vh;
  }
  .saomao {
    display: flex;
    width: 100%;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 70px;
    .left {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      position: relative;
      img {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
      }
    }
    .center {
      display: flex;
      width: 147px;
      height: 48px;
      background: rgba(255, 76, 35, 1);
      border-radius: 24px;
      justify-content: center;
      align-items: center;
      color: #fff;
      i {
        display: block;
        width: 20px;
        height: 20px;
        background: url(../assets/images/home_Sweep@2x.png) no-repeat;
        background-size: 100% 100%;
        margin-right: 10px;
      }
    }
    .right {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      position: relative;
      img {
        width: 20px;
        height: 20px;
        position: absolute;
        top: 0;
        bottom: 0;
        right: 0;
        left: 0;
        margin: auto;
      }
    }
  }
}
</style>



